// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;
$color-torch-red:                   #ff1553;

$color-sapphire:                    #102654;
$color-crimson:                     #d10a42;
$color-chambray:                    #495a7d;
$color-egyptian-blue:               #103aae;
$color-torch-red:                   #ed0233;
$color-dark-grey:                   #afafaf;
$color-ghost-white:                 #f8f9fc;
$color-light-slate-grey:            #74819c;
$color-resolution-blue:             #1c3374;
$color-black-russian:               #030a1a;
$color-whisper:                     #e8e8e8;
$color-spindle:                     #b6bdcc;
$color-tory-blue:                   #2e35ac;
$color-crimson-opacity:             rgba(209, 10, 66, 0.2);
$color-resolution-blue-opacity:     rgba(28, 51, 116, 0.2);
$color-sapphire-opacity:            rgba(16, 38, 84, 0.89);
$color-grey-opacity:                rgba(120, 120, 120, 0.2);
$color--dark-cerulean-opacity:      rgba(20, 54, 122, 0.5);
$color-whisper-opacity:             rgba(232, 232, 232, 0.48);

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$blue-to-red:                       linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
$red-to-blue:                       linear-gradient(80deg, #ed0233 20%, #003eb7 90%);
$red-to-blue-numb:                  linear-gradient(127.61deg, #d10a42 0%, #0041ca 45.58%);
$red-to-blue-numb-second:           linear-gradient(142.51deg, #d10a42 0%, #0041ca 87.33%);
$red-to-blue-numb-third:            linear-gradient(116deg, rgba(209, 10, 66, 1) 0%, rgba(0, 53, 159, 1) 116%);
$dark-red-to-sapphire:              linear-gradient(rgba(237, 2, 51, 0.2) rgba(0, 62, 183, 0.2));
$red-to-egyptian-blue:              linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
$conic-gradient-desktop:            conic-gradient(from 150.87deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
$conic-gradient-tablet:             conic-gradient(from 155.49deg at 20.7% 35.74%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
$conic-gradient-mobil:              conic-gradient(from 167.26deg at 35.31% 34.22%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-placeholder:                    "Placeholder", "Arial", sans-serif;

$ff-roboto:                         "RobotoCondensed", "Arial", sans-serif;
$ff-alumni:                         "AlumniSans", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

$trans-scale3d:                     scale3d(1.02, 1.02, 1.02);
$trans-scale:                       scale(1.02);

$trans-easeinout:                   all 0.5s ease-in-out;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-767:                            767px;
$vp-768:                            768px;
$vp-1023:                           1023px;
$vp-1024:                           1024px;
$vp-1199:                           1199px;
$vp-1200:                           1200px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
